import { createSlice } from "@reduxjs/toolkit";

const jvscrwSlice = createSlice({
  name: "jvsform",

  initialState: {
    plantilla_item: "",
    office: {
      ofc_name: "",
      ofc_head: "",
    },
    position: {
      title: "",
      salary_grade: "",
    },
    eligibility: {
      std_quantity: "",
      std_keyword: "",
      std_specifics: "",
    },
    education: {
      std_quantity: "",
      std_keyword: "",
      std_specifics: "",
    },
    experience: {
      std_quantity: "",
      std_keyword: "",
      std_specifics: "",
    },
    training: {
      std_quantity: "",
      std_keyword: "",
      std_specifics: "",
    },

    competencies: {
      com_education: {},
      com_writtenExam: {},
      com_computationSKills: {},
      com_oralExam: {},
      com_creativeWork: {},
      com_analyticalSkills: {},
      com_training: {},
      com_others: {},
      com_experience: {},
    },

    refresh: false,
  },

  reducers: {
    setPlantilla: (state, action) => {
      state.plantilla_item = action.payload;
    },
    setOffice: (state, action) => {
      let { ofc_name, ofc_head } = action.payload;
      state.office = {
        ofc_name: ofc_name,
        ofc_head: ofc_head,
      };
    },
    setPosition: (state, action) => {
      let { title, salary_grade } = action.payload;
      state.position = {
        title: title,
        salary_grade: salary_grade,
      };
    },
    setEligibility: (state, action) => {
      let { std_quantity, std_keyword, std_specifics } = action.payload;
      state.eligibility = {
        std_quantity: std_quantity,
        std_keyword: std_keyword,
        std_specifics: std_specifics,
      };
    },
    setEducation: (state, action) => {
      let { std_quantity, std_keyword, std_specifics } = action.payload;
      state.education = {
        std_quantity: std_quantity,
        std_keyword: std_keyword,
        std_specifics: std_specifics,
      };
    },
    setWorkExp: (state, action) => {
      let { std_quantity, std_keyword, std_specifics } = action.payload;
      state.experience = {
        std_quantity: std_quantity,
        std_keyword: std_keyword,
        std_specifics: std_specifics,
      };
    },
    setTraining: (state, action) => {
      let { std_quantity, std_keyword, std_specifics } = action.payload;
      state.training = {
        std_quantity: std_quantity,
        std_keyword: std_keyword,
        std_specifics: std_specifics,
      };
    },

    setCompetencies: (state, action) => {
      let {
        com_education,
        com_writtenExam,
        com_computationSKills,
        com_oralExam,
        com_creativeWork,
        com_analyticalSkills,
        com_training,
        com_others,
        com_experience,
      } = action.payload;

      state.competencies = {
        com_education,
        com_writtenExam,
        com_computationSKills,
        com_oralExam,
        com_creativeWork,
        com_analyticalSkills,
        com_training,
        com_others,
        com_experience,
      };
    },

    setRefreh: (state) => {
      state.refresh = !state.refresh;
    },
  },
});

export const {
  setEligibility,
  setEducation,
  setWorkExp,
  setTraining,
  setOffice,
  setPlantilla,
  setPosition,
  setCompetencies,
  setRefreh,
} = jvscrwSlice.actions;

export default jvscrwSlice.reducer;
