import React from "react";
import InputComponent from "../../../../../common/input_component/input_component/input_component";
import TextAreaComponent from "../../../../../common/input_component/textarea_input_component/textarea_input_component";
import ModalComponent from "../../../../../common/modal_component/modal_component";
import { useFormik } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { API_HOST } from "../../../../../../helpers/global/global_config";

const CalibratedScaleModal = ({
  title,
  onClose,
  isDisplay,
  type,
  jvsId,
  com_specific = null,
  rtgSequence = null,
}) => {
  const sequence = rtgSequence === null ? "" : "/" + rtgSequence;
  const data = useFormik({
    initialValues: {
      rtg_factor: "",
      rtg_percent: "",
      com_type: type,
      com_specific: com_specific === null ? "default" : com_specific,
    },
    validationSchema: Yup.object({
      rtg_factor: Yup.string()
        .required("This field is required")
        .max(255, "Invalid input"),

      rtg_percent: Yup.number()
        .typeError("Must be a number")
        .required("This field is required")
        .max(100, "Invalid input"),
    }),
    onSubmit: async (values) => {
      console.log(values);
      await axios
        .post(
          API_HOST +
            "jvscrw-competency-rating/" +
            jvsId +
            "/sequence" +
            sequence,
          values
        )
        .then(() => {
          console.log("Success");
        })
        .catch((err) => {
          console.log(err);
        });
      onClose();
    },
  });

  return (
    <React.Fragment>
      <ModalComponent
        title={title}
        onSubmitName="Save"
        onCloseName="Delete"
        onClose={onClose}
        isDisplay={isDisplay}
        onClose={onClose}
        onSubmit={data.handleSubmit}
        onSubmitType="Submit"
      >
        <div>
          <label>Scale Factor</label>
          <TextAreaComponent
            name="rtg_factor"
            row="3"
            onChange={data.handleChange}
          />

          {data.touched.rtg_factor && data.errors.rtg_factor ? (
            <p className="error-validation-styles">{data.errors.rtg_factor}</p>
          ) : null}
        </div>
        <br />
        <div>
          <label>Percentage Scale</label>
          <InputComponent name="rtg_percent" onChange={data.handleChange} />
          {data.touched.rtg_percent && data.errors.rtg_percent ? (
            <p className="error-validation-styles">{data.errors.rtg_percent}</p>
          ) : null}
        </div>
      </ModalComponent>
    </React.Fragment>
  );
};

export default CalibratedScaleModal;
